#board {
    display: grid;
    grid-template-columns: repeat(5, 200px);
    grid-template-rows: repeat(5, 200px);
    width : 1000px;
    height : 1000px;
    background-color: blue;
}

.tile-highlight::before {
    content: " ";
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: rgba(0,0,0,0.5);
}